import { Button, Box, Paper } from '@mui/material';
import React from 'react';
import { FaRedoAlt, FaRegEdit, FaTimes } from 'react-icons/fa';
// Page Components
import InnerDialog from '../Dialog/InnerModalDialog';
import FileDropzone from '../FileDropzone/FileDropzone';

export default function EditImageModal(props) {

    const [isEdit, setIsEdit] = React.useState(false);
    const toggleIsEdit = () => setIsEdit(!isEdit);


    const handleEdit = () => {

    }

    return (
        <InnerDialog open={props.open} toggle={props.toggle} title="Edit File">
            <Box width={400} height={300} display="flex" justifyContent="center">
                {!isEdit &&
                    <Paper sx={{ m: 1, p: 2 }}>
                        <img
                            width={props.width ?? 250}
                            height={props.height ?? 250}
                            src={props.src ?? ''}
                            alt={props.alt ?? 'Image'}
                            loading="lazy"
                            style={{ cursor: 'pointer' }}
                        />
                    </Paper>
                }
                {isEdit &&
                    <FileDropzone maxFiles={1} height={props.height ?? 250} width={props.width ?? 250} title="Select or drop new file here" />
                }
            </Box>
            <Box display="flex" justifyContent="center">
                {!isEdit &&
                    <Button variant="contained" onClick={toggleIsEdit} sx={{ minWidth: 150 }}>
                        <FaRedoAlt style={{ marginRight: 5 }} size={14} />
                        Change
                    </Button>
                }
                {isEdit &&
                    <React.Fragment>
                        <Button variant="contained" sx={{ minWidth: 150 }}>
                            <FaRegEdit style={{ marginRight: 5 }} size={14} />
                            Edit
                        </Button>
                        <Button onClick={toggleIsEdit} variant="contained" color='secondary' sx={{ minWidth: 150, ml: 1 }}>
                            <FaTimes style={{ marginRight: 5 }} size={14} />
                            Cancel
                        </Button>
                    </React.Fragment>
                }
            </Box>
        </InnerDialog>
    )
}
