import React from 'react'
import config from '../../../config/config';
import Scrollbars from 'react-custom-scrollbars-2';
// Redux
import { useSelector } from 'react-redux';
// MUI Components
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
// Page Components
import NoData from '../../components/shared/NoData/NoData';
// react-icons
import { FaUpload } from 'react-icons/fa';
import ImageBox from '../shared/ImageBox/ImageBox';
import UploadFileModal from '../modals/UploadFileModal';

export default function SignaturesList() {

    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);
    const role = authRedux.role.id;

    const [isUpload, setIsUpload] = React.useState(false);
    const toggleIsUpload = () => setIsUpload(!isUpload);

    let me = authRedux.me;
    let attachments = memberRedux?.selectedMember?.attachments;

    if (role === 5) {
        attachments = me.attachments;
    }

    let list = attachments.filter((data, index) => {
        return data.attachment_type_id === 1;
    })

    return (
        <div>
            {isUpload &&
                <UploadFileModal title={"Upload New Signature"} open={isUpload} toggle={toggleIsUpload} />
            }
            {list.length <= 0 &&
                <Box sx={{ height: 200 }}>
                    <Box sx={{ zIndex: 1 }}>
                        <NoData event={toggleIsUpload} title="No Signature Attachment Found" />
                    </Box>
                </Box>
            }
            {list.length > 0 &&
                <Paper elevation={0} sx={{ p: 1, m: 1 }}>
                    <Box sx={{ mb: 1 }}>
                        <Button
                            sx={{
                                pt: 0.5,
                                pb: 0.5,
                                width: 150,
                                fontSize: 14,
                            }}
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            onClick={toggleIsUpload}
                        >
                            Upload
                            <FaUpload size={13} style={{ marginLeft: 5 }} />
                        </Button>
                    </Box>
                    <Scrollbars style={{ overflowY: 'hidden', width: '100%', height: 195, backgroundColor: '#eee' }}>
                        <Box
                            display="flex"
                            flexDirection="row"
                            style={{
                                width: '100%',
                                m: 2,
                                height: '100%'
                            }}
                        >
                            {
                                list.map((data, index) => (
                                    <React.Fragment key={`imgBoxEsig-${index}`}>
                                        <ImageBox
                                            width={170}
                                            height={170}
                                            src={`${config.RESOURCES}users/${data.file_path}/${data.file_name}`}
                                            alt={data.file_name}
                                        />
                                    </React.Fragment>

                                ))
                            }
                        </Box>
                    </Scrollbars>
                </Paper>
            }
        </div>
    )
}
