import React from 'react';
import { Box, Button, Link } from '@mui/material';
import { FaExpand, FaFileDownload, FaRegEdit, FaTrashAlt } from 'react-icons/fa';
import ExpandImageModal from './ExpandImageModal';
import EditImageModal from './EditImageModa';
import alertService from '../../../../services/alert/alertService';
import toastService from '../../../../services/toast/toastService';

export default function ImageBox(props) {

    const [isHover, setIsHover] = React.useState(false);
    const [isExpand, setIsExpand] = React.useState(false);
    const toggleIsExpand = () => setIsExpand(!isExpand);
    const [isEdit, setIsEdit] = React.useState(false);
    const toggleIsEdit = () => setIsEdit(!isEdit);

    const handleRemoveFile = async (fileName) => {
        try {

            let action = await alertService.confirm({ title: "Remove this file", text: "Are you sure?" });

            if (action.isConfirmed) {

            }

        } catch (e) {
            toastService.error(e.toString());
        }
    }

    return (
        <React.Fragment>
            {isExpand &&
                <ExpandImageModal open={isExpand} toggle={toggleIsExpand} src={props.src} alt={props.alt} />
            }
            {isEdit &&
                <EditImageModal open={isEdit} toggle={toggleIsEdit} src={props.src} alt={props.alt} />
            }
            <Box
                onMouseOverCapture={() => setIsHover(true)}
                onMouseLeave={() => setTimeout(() => setIsHover(false), 100)}
                sx={{
                    '&:hover': {
                        cursor: 'pointer'
                    }
                }}
            >
                {isHover &&
                    <React.Fragment>
                        <Box
                            width={props.width ?? 170}
                            height={props.height ?? 170}
                            sx={{
                                backgroundColor: "#000",
                                opacity: 0.5,
                                m: 1,
                                zIndex: 3,
                                position: 'absolute',
                                textAlign: 'right'
                            }}>
                        </Box>
                        <Button
                            sx={{
                                zIndex: 4,
                                position: 'absolute',
                                opacity: 1,
                                mt: 1,
                            }}
                            disableFocusRipple
                            disableRipple
                            disableTouchRipple
                            onClick={toggleIsExpand}
                        >
                            <FaExpand color='#FFF' size={20} />
                        </Button>
                        <Link
                            component={Button}
                            sx={{
                                zIndex: 4,
                                position: 'absolute',
                                opacity: 1,
                                mt: 5,
                            }}
                            disableFocusRipple
                            disableRipple
                            disableTouchRipple
                            target="_blank"
                            download
                            href={props.src}
                        >
                            <FaFileDownload color='#FFF' size={21} />
                        </Link>
                        <Button
                            sx={{
                                zIndex: 4,
                                position: 'absolute',
                                opacity: 1,
                                mt: 9,
                                ml: 0.2
                            }}
                            disableFocusRipple
                            disableRipple
                            disableTouchRipple
                            onClick={toggleIsEdit}
                        >
                            <FaRegEdit color='#FFF' size={21} />
                        </Button>
                        <Button
                            sx={{
                                zIndex: 4,
                                position: 'absolute',
                                opacity: 1,
                                mt: 12.5,
                            }}
                            disableFocusRipple
                            disableRipple
                            disableTouchRipple
                            onClick={() => handleRemoveFile(props.fileName)}
                        >
                            <FaTrashAlt color='#FFF' size={21} />
                        </Button>
                    </React.Fragment>

                }
                <img
                    width={170}
                    height={170}
                    src={props.src ?? ''}
                    alt={props.alt ?? 'Image'}
                    loading="lazy"
                    style={{ margin: 10, padding: 1, cursor: 'pointer' }}
                />
            </Box>
        </React.Fragment>
    )
}
