import React from 'react';
import { Box, Button } from '@mui/material';
import FileDropzone from '../shared/FileDropzone/FileDropzone';
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import { FaTimes, FaUpload } from 'react-icons/fa';

export default function UploadFileModal(props) {
    return (
        <InnerModalDialog title={props.title ?? "New Files"} open={props.open} toggle={props.toggle}>
            <Box sx={{ width: props.width ?? 500, height: props.height ?? 260, mt: 2 }}>
                <FileDropzone title={props.dropTitle ?? "Select or drop files here"} width={props.width ?? 500} height={props.height ?? 200}  />
                <Box display="flex" flexDirection="row" justifyContent="center" sx={{ mt: 2 }}>
                    <Button variant="contained">
                        Upload <FaUpload size={14} style={{ marginLeft: 5 }} />
                    </Button>
                    <Button onClick={props.toggle} variant="contained" color="secondary" sx={{ ml: 1 }}>
                        Cancel <FaTimes size={14} style={{ marginLeft: 5 }} />
                    </Button>
                </Box>
            </Box>
        </InnerModalDialog>
    )
}
