import React from 'react'
import config from '../../../config/config';
import Scrollbars from 'react-custom-scrollbars-2';
// Redux
import { useSelector } from 'react-redux';
// MUI Components
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
// Page Components
import NoData from '../../components/shared/NoData/NoData';
// react-icons 
import ImageBox from '../shared/ImageBox/ImageBox';

export default function CollateralsList() {

    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);
    const role = authRedux.role.id;

    let me = authRedux.me;
    let attachments = memberRedux?.selectedMember?.attachments;

    if (role === 5) {
        attachments = me.attachments;
    }

    let list = attachments.filter((data, index) => {
        return data.attachment_type_id === 4;
    })

    return (
        <div>
            {list.length <= 0 &&
                <Box sx={{ height: 200 }}>
                    <Box sx={{ zIndex: 1 }}>
                        <NoData canUpload={false} title="No Collateral Attachment Found" />
                    </Box>
                </Box>
            }
            {list.length > 0 &&
                <Paper elevation={0} sx={{ p: 1, m: 1 }}>
                    <Scrollbars style={{ overflowY: 'hidden', width: '100%', height: 195, backgroundColor: '#eee' }}>
                        <Box
                            display="flex"
                            flexDirection="row"
                            style={{
                                width: '100%',
                                m: 2,
                                height: '100%'
                            }}
                        >
                            {
                                list.map((data, index) => (
                                    <React.Fragment key={`imgBoxEsig-${index}`}>
                                        <ImageBox
                                            width={170}
                                            height={170}
                                            src={`${config.RESOURCES}users/${data.file_path}/${data.file_name}`}
                                            alt={data.file_name}
                                        />
                                    </React.Fragment>

                                ))
                            }
                        </Box>
                    </Scrollbars>
                </Paper>
            }
        </div>
    )
}
